import { Component, OnInit } from '@angular/core';
import {DataService} from '../data.service';
import { Book } from '../Book';
import {ActivatedRoute, ParamMap,Router} from "@angular/router";

import { WishList } from '../wishList';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css']
})
export class NavComponent implements OnInit {
	userName:string;
  messageCount:number;
  cartCount:number=0;
  userId={Id:""};
  cartBook:Book[];
  constructor(private dataService: DataService,private router: Router,) { }
  ngOnInit() {
  	let loginStatus=localStorage.getItem('loggedIn') || false;
  	if(loginStatus == false){
  		document.getElementById('loggedIn').style.display='none';
      if(localStorage.getItem('wishList') != null){
      this.cartCount=JSON.parse(localStorage.getItem('wishList')).length;
       
  	}
  }
  	else{
       if(localStorage.getItem('wishList') != null){
         this.cartCount=JSON.parse(localStorage.getItem('wishList')).length;
       this.cartBook= JSON.parse(localStorage.getItem('wishList'));
              for(let cart of this.cartBook){
                 let wishItem:WishList={
                      bookBId: cart.bId,
                      userUId:JSON.parse(localStorage.getItem('user')).uId
                  }
                  this.dataService.addWishList(wishItem).subscribe(cart=>{
                    this.userName=JSON.parse(localStorage.getItem('user')).name || '';
                    if(JSON.parse(localStorage.getItem('user'))['messages'] != null)
                      this.messageCount=JSON.parse(localStorage.getItem('user'))['messages'].length;
                   else{
                     this.messageCount=0;
                     }
                    this.userId.Id= JSON.parse(localStorage.getItem('user')).uId;
                    this.dataService.getUserCart(this.userId).subscribe(cart=>{
                      this.cartCount=cart['count'];
                    })
                  })
              }
              localStorage.removeItem('wishList')
       }
       else{
          this.userName=JSON.parse(localStorage.getItem('user')).name || '';
          //console.log((localStorage.getItem('user'))['messages'])
          if(JSON.parse(localStorage.getItem('user'))['messages'] != null)
          this.messageCount=JSON.parse(localStorage.getItem('user'))['messages'].length || 0;
           else{
             this.messageCount=0;
           }
          this.userId.Id= JSON.parse(localStorage.getItem('user')).uId;
          this.dataService.getUserCart(this.userId).subscribe(cart=>{
             this.cartCount=cart['count'];
          })
       }
      document.getElementById('account').style.display='none';
  	}
  }


}

