import { Component, OnInit } from '@angular/core';
import { DataService} from '../data.service';
import {ActivatedRoute, ParamMap,Router} from '@angular/router';

import { Book } from '../Book';
import { WishList } from '../wishList';

@Component({
  selector: 'app-remove-item',
  templateUrl: './remove-item.component.html',
  styleUrls: ['./remove-item.component.css']
})
export class RemoveItemComponent implements OnInit {
	bookId:number;
  wishList:Book[];
  newWishList:Book[];
  constructor(private router: Router,private active:ActivatedRoute, private dataservice: DataService) { }

  ngOnInit() {
  	 this.active.paramMap.subscribe( (params: ParamMap) =>{
      this.bookId = parseInt(params.get('id'));
    });
  	 if(!localStorage.getItem('loggedIn')){
  	this.wishList =JSON.parse(localStorage.getItem("wishList")) || [];
    this.wishList.splice(this.bookId,1)
    localStorage.setItem('wishList',JSON.stringify(this.wishList));
    this.router.navigate(['/wishList']);
    }
    if(localStorage.getItem('loggedIn')){
    	this.dataservice.deleteCartItem(this.bookId).subscribe(item=>{
    		this.router.navigate(['/wishList']);
    	});
    }

  }

}
