import { Component, OnInit } from '@angular/core';
import { Book } from '../Book';
import { FileUploader, FileSelectDirective } from 'ng2-file-upload/ng2-file-upload';
import { FormGroup,Validators, FormControl} from '@angular/forms';
import {DataService} from '../data.service'
import {Router} from '@angular/router';

const URL = 'api/books/upload';

@Component({
  selector: 'app-add-book',
  templateUrl: './add-book.component.html',
  styleUrls: ['./add-book.component.css']
})
export class AddBookComponent implements OnInit {

  constructor(private dataService: DataService,private router: Router) { }
  path:string='';
  errorStatus: boolean;
  book:Book;
   form: FormGroup;
  public uploader:FileUploader = new FileUploader({url:URL, itemAlias:'photo'});
  addBook(form){
    this.uploader.uploadAll();
    this.book=form.value
  
}
add(){
		this.book.image=this.path;
		let book:Book ={
  		name:this.book.name,
  		authorName:this.book.authorName,
  		price:this.book.price,
  		image:this.path,
  		condition:this.book.condition,
  		userId:JSON.parse(localStorage.getItem('user')).uId
  	}
  		this.dataService.addBook(book).subscribe(book =>{
  		    if(book!=null){ 
            console.log(book);
            this.router.navigate(['profile']);
          }
          else {
            console.log('in else')
            this.errorStatus=true;
            this.router.navigate(['addListing']);
          }
          console.log(this.errorStatus);
  		});
}
  ngOnInit() {
    this.form = new FormGroup({
      name: new FormControl('',[Validators.required,Validators.pattern('[a-zA-z]+( [a-zA-Z]+)*')]),
      authorName:new FormControl('',[Validators.required,Validators.pattern('[a-zA-z]+( [a-zA-Z]+)*')]),
      price:new FormControl('',Validators.required),
      photo:new FormControl('',Validators.required)
    })
  	this.uploader.onAfterAddingFile = (file)=>{file.withCredentials = false};
  	this.uploader.onCompleteItem = (item:any, response:any,status:any,header:any)=>{
  		console.log('in Uploader')
  		this.path=response;
      console.log(this.path);
  		 this.path=this.path.substring(7,this.path.length);
       this.add();
  	}
  }

}
