import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FileSelectDirective } from 'ng2-file-upload';
import { ReactiveFormsModule } from '@angular/forms';
import {Routes, RouterModule} from '@angular/router';

import { AppComponent } from './app.component';
import { SignUpComponent } from './sign-up/sign-up.component';

import {FormsModule} from '@angular/forms';

import {DataService} from './data.service'

import {HttpClientModule} from '@angular/common/http'

import {HttpModule} from '@angular/http';

import { SignInComponent } from './sign-in/sign-in.component';
import { LogOutComponent } from './log-out/log-out.component';
import { NavComponent } from './nav/nav.component';
import { BodyComponent } from './body/body.component';
import { ProfileComponent } from './profile/profile.component';
import { AddBookComponent } from './add-book/add-book.component';
import { MessageComponent } from './message/message.component';
import { BookComponent } from './book/book.component';
import { SentMessageComponent } from './sent-message/sent-message.component';
import {  WishListComponent } from './wish-list/wish-list.component';
import { RemoveItemComponent } from './remove-item/remove-item.component';
const ROUTES: Routes= [
  { path:'', component:BodyComponent}, 
  { path:'signUp', component:SignUpComponent},
  { path:'signIn', component:SignInComponent},
  { path:'logOut', component:LogOutComponent},
  { path:'profile', component:ProfileComponent},
  { path:'addListing', component:AddBookComponent},
  { path: 'message' , component:MessageComponent},
  { path: 'listings/:id' , component:BookComponent},
  { path: 'message/:id/:uId' , component:SentMessageComponent},
  { path: 'wishList' , component:WishListComponent},
  { path: 'remove/:id' , component:RemoveItemComponent}

]

@NgModule({
  declarations: [
    AppComponent,
    SignUpComponent,
    SignInComponent,
    LogOutComponent,
    NavComponent,
    BodyComponent,
    ProfileComponent,
    AddBookComponent,
    FileSelectDirective,
    MessageComponent,
    BookComponent,
    SentMessageComponent,
    WishListComponent,
    RemoveItemComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    RouterModule.forRoot(ROUTES),
  ],
  providers: [DataService],
  bootstrap: [AppComponent]
})
export class AppModule { }
