import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable } from 'rxjs';
import {User} from './User';
import { Book } from './Book';
import { Message } from './message';
import { WishList } from './wishList';

const httpOptions = {
   headers: new HttpHeaders({ 'Content-Type': 'application/json' })
 };

@Injectable({
  providedIn: 'root'
})


export class DataService {

  cartId={Id:""};

  constructor(private http: HttpClient) { }

   addUser(user:User):Observable<User>{
       return this.http.post<User>('api/users',user, httpOptions);
  }
  signIn(user:User):Observable<User>{
      return this.http.post<User>('api/users/signIn',user, httpOptions);
  }

  addBook(book:Book):Observable<Book>{
       return this.http.post<Book>('api/books',book, httpOptions);
  }

  getAllBooks():Observable<Book[]>{
    return this.http.get<Book[]>('api/books');
  }
  getAllFilterByLowToHigh():Observable<Book[]>{
    return this.http.get<Book[]>('api/books/filterLowToHigh');
  }
  getAllFilterByHighToLow():Observable<Book[]>{
    return this.http.get<Book[]>('api/books/filterHighToLow');
  }
  getAllFilterByCondition(condition:object):Observable<Book[]>{
    return this.http.post<Book[]>('api/books/filterCondition',condition,httpOptions);
  }

  getAllSearchByName(name:object):Observable<Book[]>{
    console.log(name)
    return this.http.post<Book[]>('api/books/searchByName',name,httpOptions);
  }

  getBook(id:object){
    return this.http.post<Book>('api/books/book',id,httpOptions);
  }
  sentMessage(message:Message){
    console.log(message)
    return this.http.post<Book>('api/message',message,httpOptions);
  }

  addWishList(wishList:WishList){
     return this.http.post<WishList>('api/carts',wishList,httpOptions); 
  }

  getUserCart(id:object){
     return this.http.post<WishList>('api/carts/getCart',id,httpOptions);  
  }
   getUserBook(id:object):Observable<Book[]>{
     return this.http.post<Book[]>('api/books/userBooks',id,httpOptions);  
  }
  deleteCartItem(bookId:number):Observable<WishList[]>{
    const url = 'api/carts/'+`${bookId}`;
    return this.http.delete<WishList[]>(url);
  }
}
