import { Component, OnInit } from '@angular/core';
import { Message } from '../message'
import { DataService} from '../data.service';
import {ActivatedRoute, ParamMap,Router} from "@angular/router";
@Component({
  selector: 'app-sent-message',
  templateUrl: './sent-message.component.html',
  styleUrls: ['./sent-message.component.css']
})
export class SentMessageComponent implements OnInit {
  bookId:number;
  userId:number;
  success:boolean=false;
  constructor(private dataservice:DataService, private active:ActivatedRoute,private router: Router) { }
  sentMessage(frm){
  	
  	let message:Message={
  		message:frm.value.message,
  		userUId:this.userId,
  		bookBId:this.bookId
  	}
  	console.log(message);
  	this.dataservice.sentMessage(message).subscribe(message =>{
      console.log(message);
      this.success=true;
      frm.reset();
    })
  }
  ngOnInit() {
  	this.active.paramMap.subscribe( (params: ParamMap) =>{
      this.bookId = parseInt(params.get('id'));
      this.userId=  parseInt(params.get('uId'));
      console.log(this.bookId," ",this.userId);
    });
    let loginStatus=localStorage.getItem('loggedIn') || false;
  	if(loginStatus == false){
  		this.router.navigate(['signIn']);
  	}
  }
  }
