import { Component, OnInit } from '@angular/core';
import { DataService } from'../data.service';
import { Book } from '../Book'
@Component({
  selector: 'app-wish-list',
  templateUrl: './wish-list.component.html',
  styleUrls: ['./wish-list.component.css']
})
export class WishListComponent implements OnInit {
  constructor(private dataService:DataService) { }
 userId={Id:""};
 wishListBook:Book[];
 wishListTotalAmount=0;
 loggedInStatus:boolean;
  ngOnInit() {
  	this.loggedInStatus= JSON.parse(localStorage.getItem('loggedIn')) || false;
  	if(this.loggedInStatus){
    	this.userId.Id= JSON.parse(localStorage.getItem('user')).uId;
  	this.dataService.getUserCart(this.userId).subscribe(cart=>{
       this.wishListBook=cart['rows'];  
    for(let book of this.wishListBook){
    this.wishListTotalAmount=this.wishListTotalAmount+book['book'].price;
    }    
     })
  }
  else{
  	this.wishListBook= JSON.parse(localStorage.getItem('wishList')); 
  for(let book of this.wishListBook){
    this.wishListTotalAmount+=book.price;
  }
  }
 }
}
