import { Component, OnInit } from '@angular/core';
import {User} from '../User'
import { FormGroup,Validators, FormControl} from '@angular/forms';
import {DataService} from '../data.service'

import {Router} from '@angular/router';
import { AppComponent } from '../app.component';

import { Book } from '../Book'

import { WishList } from '../wishList';
@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.css'],
  providers: [DataService],
})
export class SignUpComponent implements OnInit {
   cartBook:Book[];
   form: FormGroup;
  constructor(private dataService: DataService,private router: Router, private app:AppComponent) {

   }

errorStatus: boolean;
  addUser(form){
    console.log(form.value);
  		let loginStatus:boolean;
  	let userName:string;
  	let user:User ={
  		name:form.value.name,
  		email:form.value.email,
  		college:form.value.college,
  		address:form.value.address,
  		mobile:form.value.mobile,
  		password:form.value.password
  	}
  		this.dataService.addUser(user).subscribe(user =>{
  		    if(user!=null){
            localStorage.setItem('loggedIn','true')
            localStorage.setItem('user',JSON.stringify(user)); 
            this.router.navigate(['/profile']);
          }
          else {
            this.errorStatus=true;
            this.router.navigate(['signUp']);
          }
  		});

  }


  ngOnInit() {
    this.form = new FormGroup({
      name: new FormControl('',[Validators.required,Validators.pattern('[a-zA-z]+( [a-zA-Z]+)*')]),
      email: new FormControl('',[Validators.required,Validators.email]),
      college:new FormControl('',[Validators.required,Validators.pattern('[a-zA-z]+( [a-zA-Z]+)*')]),
      address:new FormControl('',[Validators.required,Validators.pattern('[a-zA-Z0-9\\.\/_;,-]+( [a-zA-Z0-9\\.\/_;,-]+)*')]),
      mobile: new FormControl('',[Validators.required,Validators.pattern('[0-9]*'),Validators.minLength(10),Validators.maxLength(10)]),
      password:new FormControl('',[Validators.required,Validators.pattern('[a-zA-Z0-9@!#$%&*]*')])
    })
  }

}
