import { Component, OnInit } from '@angular/core';
import { DataService} from '../data.service';
import {ActivatedRoute, ParamMap,Router} from "@angular/router";
import { Book } from '../Book';
import { WishList } from '../wishList';

@Component({
  selector: 'app-book',
  templateUrl: './book.component.html',
  styleUrls: ['./book.component.css']
})
export class BookComponent implements OnInit {
	book:Book;
	bookId={Id:""};
   wishList:Book[];
   booksListing:Book[];
  constructor(private dataservice:DataService, private active:ActivatedRoute,private router: Router,) { }
addToCart(event){
    let bookId = event.target.attributes.id.value;
    if(!localStorage.getItem('loggedIn')){
    this.wishList =JSON.parse(localStorage.getItem("wishList")) || [];
    this.wishList.push(this.booksListing[bookId-1]);
    localStorage.setItem('wishList',JSON.stringify(this.wishList));
    this.router.navigate(['/wishList']);
  }
  else{
    let wishItem:WishList={
        bookBId: bookId,
        userUId:JSON.parse(localStorage.getItem('user')).uId
    }
    console.log(wishItem)
    this.dataservice.addWishList(wishItem).subscribe(cart=>{
       this.router.navigate(['/wishList']);
     })
  }
  }
  ngOnInit() {
  	console.log(this.active.params)
  	 this.active.paramMap.subscribe( (params: ParamMap) =>{
      this.bookId.Id = (params.get('id'));
    });
  	 this.dataservice.getBook(this.bookId).subscribe(book =>{
      this.book = book;
      console.log(this.book.name);
    })
  }

}
