import { Component, OnInit } from '@angular/core';
import { DataService} from '../data.service';
import {Router} from '@angular/router';
import { Book } from '../Book';
import { WishList } from '../wishList';
@Component({
  selector: 'app-body',
  templateUrl: './body.component.html',
  styleUrls: ['./body.component.css']
})
export class BodyComponent implements OnInit {

  booksListing:Book[];
  wishList:Book[];
  numberOfBooks:number;
  bookDate:string;
  year = new Date().getFullYear().toString();
  month = (new Date().getMonth()< 10?'0':'')+new Date().getMonth();
  day = (new Date().getDate()< 10?'0':'')+new Date().getDate();
  
  date =this.year+this.month+this.day;

  constructor(private router: Router, private dataservice: DataService) { }

  filterLowToHigh(){
    this.dataservice.getAllFilterByLowToHigh().subscribe(books =>{
      this.booksListing = books;
      this.numberOfBooks=this.booksListing.length
      console.log(books);
    })
  }

  filterHighToLow(){
    this.dataservice.getAllFilterByHighToLow().subscribe(books =>{
      this.booksListing = books;
      this.numberOfBooks=this.booksListing.length
      console.log(books);
    })
  }

  filterByCondition(frm){
    this.dataservice.getAllFilterByCondition(frm.value).subscribe(books =>{
      this.booksListing = books;
      this.numberOfBooks=this.booksListing.length
    })
  }

  SearchByName(frm){
    this.dataservice.getAllSearchByName(frm.value).subscribe(books =>{
      this.booksListing = books;
      this.numberOfBooks=this.booksListing.length
    })
  }

  addToCart(event){
    let bookId = event.target.attributes.id.value;
    if(!localStorage.getItem('loggedIn')){
    this.wishList =JSON.parse(localStorage.getItem("wishList")) || [];
    this.wishList.push(this.booksListing[bookId-1]);
    localStorage.setItem('wishList',JSON.stringify(this.wishList));
    this.router.navigate(['/wishList']);
  }
  else{
    let wishItem:WishList={
        bookBId: bookId,
        userUId:JSON.parse(localStorage.getItem('user')).uId
    }
    console.log(wishItem)
    this.dataservice.addWishList(wishItem).subscribe(cart=>{
       this.router.navigate(['/wishList']);
     })
  }
  }

  ngOnInit() {
    this.dataservice.getAllBooks().subscribe(books =>{
      this.booksListing = books;
      this.numberOfBooks=this.booksListing.length;
    })

  }
}
